.pt{
    --ptSize: 2.5rem;
    --borderSize: calc(2rem / 16);
}

section.pt{

    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    flex: 1 1 0rem;
    height: 100%;
    margin: 0rem;
    padding: 0rem;

}

.spacer{
    display: flex;
    flex-direction: row;
    flex: 1 1 0rem;
    height: var(--ptSize);
    align-items: center;
    padding: .125rem;
}

.spacer div{
    background-color: var(--color__border);
    height: var(--borderSize);
    width: 100%;

}

.end{
    flex: 0 0 5%;
}

.ptNode{
    display: flex;
    flex-direction: column;
    flex: 0 0 auto;
    align-items: center;
    width: var(--ptSize);
    gap: .25rem;
}

.ptNode p{
    text-align: center;
    color: var(--color__primary);
    overflow: visible;
    white-space: nowrap;
}

.ptBtn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: var(--ptSize);
    width: var(--ptSize);
    color: var(--color__primary);
    padding: 0rem;
    border: var(--borderSize) solid var(--color__primary);
    border-radius: var(--ptSize);
    background-color: transparent;
}

.active{
    background-color: var(--color__interactive);
    border: none;
    color: #fff;
}

.complete{
    background-color: var(--color__green);
    border: none;
    color: #fff;
}